export default function convertMarks(marks) {
  let fontSize = '';
  let fontFamily = '';
  let fontWeight = '';

  marks.forEach(mark => {
    if (mark.type === 'bold') fontWeight = 'bold';
    if (mark.type === 'sz') fontSize = mark.data.value;
    if (mark.type === 'fonts') fontFamily = mark.data.ascii;
  });

  return {
    fontSize,
    fontFamily,
    fontWeight,
  } as React.CSSProperties;
}
